.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }

  &--desktop {
    display: block;

    @include onDesktop {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }

        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
          grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    &-img {
      margin-bottom: 32px;
      width: 100%;
      transition: transform 0.3s;

      &:hover {
        transform: scale(1.1);
      }
    }

    &-title {
      margin: 0 0 8px 0;
      font-style: bold;
      font-size: 24px;
      line-height: 34px;
      color: #fff;
    }

    &-description {
      margin: 0 0 16px 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      color: #fff;
    }

    &-price {
      font-weight: 700;
      font-size: 24px;
      line-height: 34px;
      color: #fff;
    }
  }
}
